<template>
    <div>
        <common-header title="我的" />
        <div class="user_center">
            <div class="photo_panel itemblock">
                <div class="user_photo_warp">
                    <div class="user_photo"></div>
                    <p class="user_name">{{userCenterInfo.Name}}</p>
                </div>
            </div>
            <div class="user_info itemblock">
                <p class="title">我的面试情况</p>
                <div class="num_info">
                    <div class="num_info_cell">
                        <p class="num">{{userCenterInfo.HistoryRoomCount}}</p>
                        <p class="num_name">已面场次</p>
                    </div>
                    <div class="num_info_cell">
                        <p class="num">{{userCenterInfo.FutureRoomCount}}</p>
                        <p class="num_name">未面场次</p>
                    </div>
                    <div class="num_info_cell">
                        <p class="num">{{userCenterInfo.HistoryCampusCount}}</p>
                        <p class="num_name">已面人数</p>
                    </div>
                    <div class="num_info_cell">
                        <p class="num">{{userCenterInfo.FutureCampusCount}}</p>
                        <p class="num_name">未面人数</p>
                    </div>
                </div>

            </div>
            <div class="logout itemblock">
                <p @click="logout">退出当前登录</p>
            </div>
        </div>
        <common-footer :currenttype='3'/>
    </div>
</template>

<script>
import Header from './common/Header';
import Footer from './common/Footer';
import { mapGetters, mapActions } from "vuex";
export default {
    components: {
        'common-header': Header,
        'common-footer': Footer
    },
    methods: {
        ...mapActions(['getUserCenterInfo', 'logOutRequest']),
        logout() {
            let self = this;
            this.logOutRequest().then(() => {
                localStorage.clear();
                self.$router.replace({name:"Login"});
                window.location.href="/interview/login";
            }
            )
        }
    },
    computed: { ...mapGetters(["userCenterInfo"]) },
    created() {
        this.getUserCenterInfo();
    }

}
</script>

<style lang="less" scoped>
@import "../assets/css/demo.less";
.user_center {
  margin-top: 2.2rem;
  color: #354d5a;
  .itemblock {
    background: #fff;
  }
  .photo_panel {
    // height: 320/@r;
    padding: 80/@r 0 60/@r;
    .user_photo_warp {
      width: 200/@r;
      margin: 0 auto;
      text-align: center;
      .user_photo {
        display: inline-block;
        width: 120/@r;
        height: 120/@r;
        background: url("../assets/images/user_center_photo.png") no-repeat;
        background-size: contain;
      }
      .user_name {
        color: #354d5a;
        font-size: 28/@r;
        margin-top: 28/@r;
      }
    }
  }
  .user_info {
    margin-top: 14/@r;
    .title {
      border-bottom: solid 1px #e9eef0;
      padding: 26/@r 30/@r;
      font-size: 28/@r;
    }
    .num_info {
      display: table;
      width: 100%;
      padding: 0 0.75rem;
      text-align: center;
      box-sizing: border-box;
      height: 4.5rem;
      .num_info_cell {
        display: table-cell;
        vertical-align: middle;
        .num {
          color: #f29c00;
          font-size: 28/@r;
        }
        .num_name {
          font-size: 24/@r;
          margin-top: 26/@r;
          color: #707c7f;
        }
      }
    }
  }
  .logout {
    margin-top: 60/@r;
    text-align: center;
    color: #5476b8;
    font-size: 26/@r;
    line-height: 80/@r;
  }
}
</style>



